.album-cover-container {
    position: relative;
    left: calc(50% - 150px);
    width: 300px;
    height: 300px;
    border: 2px solid white;
    transition: transform 0.5s;
}
.album-cover-container:hover {
    transform: scale(1.1);
}


.album-cover-no-title-container {
    position: fixed;
    opacity: 0.7;
}

.album-cover-image {
    width: 100%;
    /* height: 100%; */
    /* object-fit: cover; */
    z-index: 1;
    position: relative;
    transition: opacity 0.3s;
}
.album-cover-container:hover .album-cover-image {
    opacity: 0;
}

.album-cover-image-no-title {
    width: 100%;
    /* height: 100%; */
    /* object-fit: cover; */
}

.album-cover-info {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-shadow: 2px 2px black, -2px -2px black, 2px -2px black, -2px 2px black, 0px 2px black, 0px -2px black, 2px 0px black, -2px 0px black;
    width: 100%;
    height: 100%;
}

